<template>
    <div class="opinion">
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <p class="title">意见反馈</p>
        <div class="liu">
            <van-field v-model="message" rows="2" autosize label="留言" type="textarea" maxlength="50" placeholder="请输入留言"
                show-word-limit />
        </div>
        <van-field v-model="phone" required label="手机号" placeholder="请输入手机号" style=" border-bottom: 1px solid #ccc;" />
        <van-button type="danger" size="large" style="margin-top: 30px;" @click="sub">
            大号按钮
        </van-button>
    </div>
</template>

<script>
import { Toast } from 'vant';
    import {
        submitAction
    } from "@/api/my/opinion/index.js"
    export default {
        data() {
            return {
                message: '',
                phone: "",
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {

        },
        methods: {
            onClickLeft() {
                history.back();
            },
            sub() {
                submitAction({
                    name: '锐哥哥',
                    content: this.message,
                    openId: localStorage.getItem('openId'),
                    phone:this.phone
                }).then(res=>{
                   if(res.data.data){
                       Toast.success('感谢您的反馈');
                   }
                })

            }
        },
    };
</script>

<style scoped lang="scss">
    .opinion {
        padding: 0px 10px;
        background-color: #fff;

        .title {
            padding: 10px;
            border-bottom: 1px solid #ccc;
        }
    }
</style>